const d = document,
    w = window,
    ls = localStorage;

d.addEventListener('DOMContentLoaded', e=> {

    eventListeners();
    darkMode();
});

function eventListeners () {
    const mobileMenu =  d.querySelector('.mobile-menu');
    mobileMenu.addEventListener('click', navegacionResponsive);

    // Muestra campos condicionales
    const metodoContacto = d.querySelectorAll('input[name="contacto[contacto]"]');
    metodoContacto.forEach(input => input.addEventListener('click', mostrarMetodosContacto));
}

function darkMode () {
    const prefiereDarkMode = window.matchMedia('{prefers-color-scheme: dark}');
    const botonDarkMode = d.querySelector('.dark-mode-boton');

    // CODIGO PARA EL DARK MODE SEGUN PREFERENCIAS DE WINDOWS DEL USUARIO
    if (prefiereDarkMode.matches) {
        d.body.classList.add('dark-mode');
    } else {
        d.body.classList.remove('dark-mode');
    }

    prefiereDarkMode.addEventListener('change', e=> {
        if (prefiereDarkMode.matches) {
            d.body.classList.add('dark-mode');
        } else {
            d.body.classList.remove('dark-mode');
        }
    });

    // CODIGO PARA EL BOTON DE DARK MODE Y MANTENERLO AL RECARGAR LA PAGINA
    botonDarkMode.addEventListener('click', e => {
        d.body.classList.toggle('dark-mode');

        if(d.body.classList.contains('dark-mode')) {
            localStorage.setItem('modo-oscuro', 'true');
        } else {
            localStorage.setItem('modo-oscuro', 'false');
        }
    });

    if (ls.getItem('modo-oscuro') === 'true') {
        d.body.classList.add('dark-mode');
    } else {
        d.body.classList.remove('dark-mode');
    }
}

function navegacionResponsive () {
    const navegacion =  d.querySelector('.navegacion');
    
    if(navegacion.classList.contains('mostrar')) {
        navegacion.classList.remove('mostrar');
    } else {
        navegacion.classList.add('mostrar');
    }
}

function mostrarMetodosContacto (e) {
    const contactoDiv = d.querySelector('#contacto');

    if(e.target.value === 'telefono') {
        contactoDiv.innerHTML = `
                <input type="tel" placeholder="Tu TelÃ©fono" id="telefono" name="contacto[telefono]">

                <p> Elija la fecha y la hora para la llamada</p>

                <label for="fecha">Fecha:</label>
                <input type="date" id="fecha" name="contacto[fecha]">

                <label for="hora">Hora:</label>
                <input type="time" id="hora" min="09:00" max="18:00" name="contacto[hora]">
        `;
    } else {
        contactoDiv.innerHTML = `
                <input type="email" placeholder="Tu Email" id="email" name="contacto[email]" >
        `;
    }
}